@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

/* 회원 공통 */
// 인증 타입 선택 버튼
.cert_type_box {
	@include mixins.justify_width();
	align-items: stretch;

	.btn_cert_type {
		@include mixins.justify_width(center, center, column, inline);
		flex: 1;
		height: 124px;
		padding: 0 14px;
		background: #fff;
		border: 1px solid variables.$colorGray03;
		border-radius: 10px;
		text-align: center;
		box-sizing: border-box;

		.text {
			display: block;
			margin-top: 12px;
			color: variables.$textColorBlack;
			@include mixins.font_size_xs;
		}

		[class^=ico_] {
			display: inline-block;
			height: 35px;
			margin-top: 2px;
			vertical-align: top;
		}

		.ico_phone {
			width: 35px;
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_cert_phone', 'png', 35px, 35px);
		}

		.ico_ipin {
			width: 37px;
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_cert_ipin', 'png', 37px, 31px);
		}

		& + .btn_cert_type {
			margin-left: 5px;
		}
	}

	& + .info_text_box {
		margin-top: 30px;
	}

	& + .info_text {
		margin-top: 10px;
	}
}

.wrapper {
	&.member_login {
		@include mixins.justify_width(center, center, column);
		min-height: 100vh;
		padding-top: 100px;
		box-sizing: border-box;

		.header_wrapper {
			width: 100%;
			border-bottom: none;

			.header_inner {
				width: 400px;
				margin: 0 auto;
			}

			.logo_box {
				width: 173px;
				height: 53px;
				margin: 0 auto;
			}
		}

		.container_wrapper {
			width: 100%;
			padding: 50px 0 100px;
			min-height: auto;

			.contents_wrap {
				width: 400px;
				margin: 0 auto;
				padding-top: 0;

				&.login {
					width: 340px;
				}

				// 비밀번호 유효성 검사(공통으로 사용되는 경우 scss 이동)
				.pw_valid_wrap {
					margin-top: 6px;

					.valid_step_box {
						@include mixins.justify_width(center);

						.valid_step_list {
							font-size: 0;

							.step_item {
								display: inline-block;
								width: 80px;
								height: 3px;
								background-color: variables.$colorGray04;
								border-radius: 2px;
								vertical-align: top;

								&:before {
									content: '';
									position: absolute;
									width: 10px;
									height: 7px;
									left: 0;
									top: 3px;
									@include mixins.ratio_images('../images/arw_valid_step', 'png', 10px, 7px);
								}

								+ .step_item {
									margin-left: 3px;
								}
							}
						}

						.bubble_speech_box {
							margin-left: auto;

							.valid_state {
								display: block;
								@include mixins.font_size_xxs;
							}
						}

						&.valid_fail {
							.valid_state {
								color: variables.$textColorBlack;
							}
						}

						&.valid_warning {
							.valid_step_list {
								.step_item {
									&:first-child {
										background-color: variables.$textColorRed;
									}
								}
							}

							.valid_state {
								color: variables.$textColorRed;
							}
						}

						&.valid_normal {
							.valid_step_list {
								.step_item {
									&:nth-child(-n + 2) {
										background-color: #ffbf00;
									}
								}
							}

							.valid_state {
								color: variables.$textColorBlack;
							}
						}

						&.valid_success {
							.valid_step_list {
								.step_item {
									background-color: variables.$kyoboGreen;
								}
							}

							.valid_state {
								color: variables.$kyoboGreen;
							}
						}
					}

					.pw_valid_list {
						margin-top: 4px;

						.pw_valid_item {
							padding-left: 14px;
							@include mixins.font_size_xxs;
							@include mixins.ratio_images('../images/arw_valid_step', 'png', 10px, 7px);
							background: {
								position: left center;
								repeat: no-repeat;
							}
							color: variables.$textColorLightGray;

							+ .pw_valid_item {
								margin-top: 6px;
							}

							&.valid_success {
								@include mixins.ratio_images('../images/arw_valid_step_success', 'png', 10px, 7px);
								color: variables.$textColorGreen;
							}

							&.valid_fail {
								@include mixins.ratio_images('../images/arw_valid_step_fail', 'png', 10px, 7px);
								color: variables.$textColorRed;
							}
						}
					}
				}

				// login
				.login_form_wrap {
					padding-bottom: 40px;
					border-bottom: 1px solid variables.$colorGray03;

					.title_wrap {
						text-align: center;

						.title_heading {
							width: 100%;

							.next_ico {
								&:after {
									content: '';
									display: inline-block;
									margin-left: 6px;
									background-repeat: no-repeat;
									background-position: center;
								}

								&.kakao {
									&.kakao {
										&:after {
											width: 24px;
											@include mixins.ratio_images('../images/ico_sns_kakao_sm', 'png', 24px, 24px);
										}
									}
								}
							}
						}

						&.title_size_md {
							.title_heading {
								.next_ico {
									&:after {
										height: 24px;
										vertical-align: -5px;
									}
								}
							}
						}
					}

					.form_col_group {
						&.valid_check {
							&.fail {
								.col_box {
									input {
										border-color: variables.$textColorRed;
									}
								}
							}

							.col_box {
								&.fail {
									input {
										border-color: variables.$textColorRed;
									}

									~ .valid_desc {
										display: block;
									}
								}

								&.id {
									.form_desc {
										display: none;
									}

									&.focus {
										.form_desc {
											display: block;
										}
									}
								}
							}

							.valid_desc {
								padding-left: 16px;
								color: variables.$textColorRed;

								&:before {
									content: '';
									display: inline-block;
									position: absolute;
									left: 0;
									top: 3px;
									width: 12px;
									height: 12px;
									background: {
										position: left top;
									}
									@include mixins.ratio_images('../images/ico_warning', 'png', 12px, 12px);
								}
							}
						}
					}

					.btn_wrap {
						margin-top: 20px;
					}

					.save_id_box {
						margin-top: 12px;
						@include mixins.justify_width(center);

						.btn_text_link {
							margin-left: auto;
						}
					}

					.sns_login_box {
						margin: 40px 0 -16px;
						text-align: center;

						.sns_login_list {
							font-size: 0;

							.sns_login_item {
								display: inline-block;
								width: 42px;
								height: 42px;
								vertical-align: top;

								.btn_sns_login {
									display: block;
									height: 100%;

									&.kakao {
										@include mixins.ratio_images('../images/ico_sns_kakao', 'png', 42px, 42px);
									}

									&.naver {
										@include mixins.ratio_images('../images/ico_sns_naver', 'png', 42px, 42px);
									}

									&.google {
										@include mixins.ratio_images('../images/ico_sns_google', 'png', 42px, 42px);
									}

									&.fb {
										@include mixins.ratio_images('../images/ico_sns_fb', 'png', 42px, 42px);
									}

									&.apple {
										@include mixins.ratio_images('../images/ico_sns_apple', 'png', 42px, 42px);
									}
								}

								~ .sns_login_item {
									margin-left: 12px;
								}
							}
						}

						.info_text {
							margin-top: 16px;
							color: variables.$textColorLightGray;
						}
					}

					.form_box, .form_col_group {
						& + .chk_row_list {
							margin-top: 20px;
							margin-bottom: 8px;
						}
					}
				}

				.join_induce_wrap {
					margin-top: 40px;
					text-align: center;

					.title_wrap {
						text-align: center;

						.title_heading {
							width: 100%;
						}

						.info_text {
							margin-top: 6px;
						}
					}

					.info_text {
						margin-top: 16px;
						color: variables.$textColorLightGray;
					}

					.btn_wrap {
						margin-top: 24px;
					}

					.col_box {
						.btn_wrap {
							margin-top: 0;
						}
					}
				}

				.login_banner_box {
					margin-top: 40px;

					.banner_link {
						img {
							display: block;
							width: 100%;
						}
					}
				}

				.captcha_wrap {
					margin-top: 20px;

					.captcha_inner {
						position: relative;
						padding-right: 40px;

						.captcha_img_box {
							width: 300px;
							height: 72px;
							border: 1px solid variables.$colorGray03;
							overflow: hidden;
							border-radius: 6px;
							background-color: #f9f9fb;
							box-sizing: border-box;

							img {
								width: 100%;
							}
						}

						.btn_wrap {
							position: absolute;
							right: 0;
							top: 0;
							width: 34px;
							height: 100%;
							margin: 0;

							.btn_captcha {
								width: 34px;
								height: 34px;
								border: 1px solid variables.$colorGray03;
								border-radius: 6px;

								.ico_refresh {
									display: inline-block;
									width: 13px;
									height: 13px;
									@include mixins.ratio_images('../images/ico_captcha_refresh', 'png', 13px, 13px);
									vertical-align: top;
								}

								.ico_speak {
									display: inline-block;
									width: 14px;
									height: 13px;
									@include mixins.ratio_images('../images/ico_captcha_speak', 'png', 14px, 13px);
									vertical-align: top;
								}

								+ .btn_captcha {
									margin: 4px 0 0;
								}
							}
						}
					}

					.form_ip {
						margin-top: 10px;
					}
				}

				// 회원 상단 아이콘 + 텍스트 안내 영역
				.member_guide_wrap {
					display: block;
					margin-bottom: 30px;

					.member_guide_title {
						@include mixins.justify_width(center);

						.ico_img_box {
							flex: 0 auto;
							margin-right: 12px;
						}

						.guide_title {
							display: inline-block;
							flex: 1;
							color: variables.$textColorBlack;
							@include mixins.font_size_def;
							font-weight: variables.$fontWeightMedium;

							.warning {
								color: variables.$textColorRed;
							}
						}
					}

					.member_guide_cont {
						margin-top: 20px;

						.info_text {
							& + .form_chk,
							& + .form_rdo {
								margin-top: 20px;
							}

							& + .bul_item_asterisk {
								margin-top: 10px;
							}
						}
					}
				}

				// 아이디 찾기 결과 리스트
				.id_result_list_wrap {
					display: block;

					.custom_scroll_wrap {
						// header : 153px, contents 상하여백 : 150px, 상단 타이틀 : 111px, 하단 안내문구 : 82px, 하단 버튼 : 90px, footer : 68px;
						max-height: calc(100vh - 153px - 150px - 111px - 82px - 90px - 68px);
					}

					// id item
					.id_result_list_item {
						@include mixins.justify_width(center);
						box-sizing: border-box;

						.left_area {
							flex: 1;

							.id_name_box {
								word-break: break-all;
								font-size: 0;

								.form_rdo {
									margin-bottom: 4px;
								}

								.id_label {
									display: inline-block;
									margin-right: 8px;
									color: variables.$textColorGray;
									@include mixins.font_size_sm;
									font-weight: variables.$fontWeightRegular;
								}

								.id_name {
									display: inline-block;
									color: variables.$textColorBlack;
									@include mixins.font_size_sm;
									font-weight: variables.$fontWeightBold;
								}
							}

							.id_info_box {
								display: block;
								margin-top: 4px;
								padding-left: 27px;

								.id_info_inner {
									display: block;
									font-size: 0;

									.id_info {
										display: inline-block;
										color: variables.$textColorLightGray;
										@include mixins.font_size_xxs;
										font-weight: variables.$fontWeightRegular;

										.date {
											font-weight: variables.$fontWeightMedium;
										}
									}

									.gap {
										display: inline-block;
										width: 1px;
										height: 9px;
										margin: 4px 8px 0;
										background-color: #ccc;
										font-size: 0;
										vertical-align: top;
									}

									.badge_sm {
										margin-left: 8px;
									}
								}
							}
						}

						.right_area {
							flex: 0 auto;
							margin-left: 20px;
						}

						& ~ .id_result_list_item {
							padding-top: 24px;
							margin-top: 24px;
							border-top: 1px solid variables.$colorGray04;
						}

						&:only-child {
							padding-bottom: 30px;
							border-bottom: 1px solid variables.$colorGray04;
						}

						&.no_rdo {
							.left_area {
								.id_info_box {
									padding-left: 0;
								}
							}
						}
					}

					.id_result_list {
						& + .id_result_list {
							margin-top: 40px;
						}
					}

					& + .info_text_box {
						margin-top: 24px;
					}
				}

				// 단순 a링크
				.btn_link_member {
					&:hover, &:focus, &:active {
						text-decoration: underline;
					}
				}

				.info_text_box {
					.btn_link_member {
						color: variables.$textColorBlack;
					}
				}

				.btn_wrap {
					&.page_bottom {
						margin-top: 40px;
					}
				}

				.fold_box_wrap {
					.fold_box_list {
						.fold_box {
							.fold_box_contents {
								.cert_type_box {
									.btn_cert_type {
										height: 115px;
									}
								}
							}

							.cert_guide_box {
								margin-top: 10px;

								.info_text {
									& + .bul_item_asterisk {
										margin-top: 6px;
									}
								}
							}
						}

					}
				}
			}
		}

		.footer_wrapper {
			width: 100%;
			height: 68px;
			@include mixins.justify_width(center, center);
			margin-top: auto;
			box-sizing: border-box;
			text-align: center;
			border-top: 1px solid variables.$colorGray04;

			.footer_inner {
				width: 400px;
				margin: 0 auto;

				.copyright {
					@include mixins.font_size_xxs;
					color: variables.$textColorLightGray;
				}
			}

		}
	}
}